import React, { Component } from 'react';
import { useTranslation, withTranslation, Trans } from 'react-i18next';
import UnityDescription from "../soft/unityDescription/UnityDescription";
import Service from "../soft/service/Service";
import Technology from "../soft/technology/Technology";
import PortfolioCompleted from "../soft/portfolioCompleted/PortfolioCompleted";
import PortfolioIoT from "./portfolioIoT/PortfolioIoT";
import Event from "../soft/event/Event";
import RedesUnity from "../soft/redesUnity/RedesUnity"
import './IoT.css';
import iotData from 'data/unit/IoT.json';

class IoT extends Component {

  render() {

    const { t, i18n } = this.props;

    return (
      <div className="IoT">
        <UnityDescription unity={iotData} />
        <p style={{ color: iotData.colorTitles }} className="IoT-title-services">{t('unit.services')}</p>
        <div className="IoT-container-services">
          <img className="IoT-background11-services" src={"https://drive.google.com/uc?id=1OfJZOqNBOsCwTCz8R3wXo2DTwpwXjJcd"} alt={"background11"}></img>
          <div className="IoT-container-background2-services">
            <img className="IoT-background21-services" src={"https://drive.google.com/uc?id=1bVe0z7Il_-RBqIgovTQAWncIgPJ-WWUn"} alt={"background21"}></img>
          </div>
          {
            iotData.services.map(
              (service, index) => {
                return <div key={index} className="IoT-container-service"> 
                <Service 
                service={service} 
                color={iotData.color} 
                colorTitle={iotData.colorSubTitles} 
                marcoServices={iotData.marcoServices} 
                /> 
                </div>
              }
            )
          }
        </div>
        <p style={{ color: iotData.colorTitles }} className="IoT-title-services"><Trans i18nKey="unit.techs">1<br />2</Trans></p>
        <div className="IoT-container-technologies">
          {
            iotData.technologies.map(
              (technology, index) => {
                return <Technology key={index} technology={technology} color={iotData.color} />
              }
            )
          }
        </div>
        <p style={{ color: iotData.colorTitles }} className="IoT-title-services">{t('unit.portfolio')}</p>
         <div className="IoT-container-portfolioCompleted">
          {
            iotData.portfolio.completed.map(
              (portfolioCompleted, index) => {
                if(index === 0){
                return  <>
                        <div className="IoT-container-background1-portfolioCompleted">
                          <img className="IoT-background1-portfolioCompleted" src={"https://drive.google.com/uc?id=1t7KwSZdS2KizI9bVon-RL_lvwV5xPJ39"} alt={"backPortafolio1"}></img>
                        </div>
                        <img className="IoT-background2-portfolioCompleted" src={"https://drive.google.com/uc?id=124mL9lUu2d-DbJTfvponggcOD12qDHc0"} alt={"backPortafolio2"}></img>
                        <p style={{ color: iotData.colorTitles }} className="IoT-completed-title">{t('unit.completed')}</p>
                        <PortfolioCompleted 
                        key={index} 
                        index={index} 
                        portfolioCompleted={portfolioCompleted} 
                        color={iotData.color} 
                        colorTitle={iotData.colorSubTitles} 
                        marcoServices={iotData.marcoServices}
                        />
                        </>
                }
                else{
                    return <PortfolioCompleted 
                    key={index} 
                    index={index} 
                    portfolioCompleted={portfolioCompleted} 
                    color={iotData.color} 
                    colorTitle={iotData.colorSubTitles} 
                    marcoServices={iotData.marcoServices}
                    />
                }
              }
            )
          }
        </div>
        <div className="IoT-container-portfolioOngoing">
          {
            iotData.portfolio.ongoing.map(
              (portfolioOngoing, index) => {
                return <PortfolioIoT 
                key={index} 
                index={index} 
                portfolioOngoing={portfolioOngoing} 
                color={iotData.color} 
                colorTitle={iotData.colorSubTitles} 
                />
              }
            )
          }
        </div>
        <p style={{ color: iotData.colorTitles }} className="IoT-title-services">{t('unit.events')}</p>
        <div className="IoT-container-events">
          {
            iotData.events.map(
              (event, index) => {
                return <Event 
                key={index} 
                event={event} 
                color={iotData.color} 
                marcoServices={iotData.marcoServices}
                />
              }
            )
          }
        </div>
        <RedesUnity redes={iotData.redes}/>
      </div>
    );
  }
}

export default withTranslation()(IoT);
